<template>
  <header class="app__header">
    <div class="app__menu" id="menu">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <div class="app__menu-header">
              <picture>
                <img @click="hideMenu" src="~@/assets/img/hideMenu.svg" alt="Закрыть меню">
              </picture>
            </div>

            <div class="app__menu-body">
              <ul class="app__menu-list">
                <li v-if="hasPerm( 'staff7Buttons' )" class="app__menu-item">
                  <router-link to="/">Кунг-фу для упров</router-link>
                </li>

                <li v-if="hasPerm( 'staff7Buttons' )" class="app__menu-item">
                  <router-link to="/">Заявки для упров</router-link>
                </li>

                <li v-if="hasPerm( 'staff7Buttons' )" class="app__menu-item">
                  <router-link to="/">Трелло для упров</router-link>
                </li>

                <li v-if="hasPerm( 'staff7Buttons' )" class="app__menu-item">
                  <router-link to="/">Логи сервера</router-link>
                </li>

                <li class="app__menu-item">
                  <router-link to="/">Ушу для админов</router-link>
                </li>

                <li class="app__menu-item">
                  <router-link to="/">Онтайм</router-link>
                </li>

                <li class="app__menu-item">
                  <router-link to="/">Админка</router-link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="container">
      <div class="row justify-content-between align-items-center">
        <div class="col-auto">

          <div class="app__hero d-flex align-items-center">
            <button class="button-menu" @click="openMenu"><img src="~@/assets/img/MenuButton.svg"></button>

            <div class="app__hero-logo">
              <router-link to="/">
                <img src="~@/assets/img/logo.svg">
              </router-link>
            </div>
          </div>
        </div>

        <div class="col-auto">
          <div class="app__navigation d-flex align-items-center">
            <span class="app__login">{{ user.login }} </span>
            <span class="app__staff">({{ user.stuff }})</span>
            <span class="app__logout">
              <button class="button button-primary button-small" @click="logout">Выйти</button>
            </span>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import {mapMutations, mapState} from 'vuex';

import PermissionMixin from "../../mixins/PermissionMixin";

export default {
  name: "AppHeader",
  methods: {
    ...mapMutations('AuthModule', [
      'logout'
    ]),
    openMenu() {
      this.$menu.classList.add( 'opened' );
    },
    hideMenu() {
      this.$menu.classList.remove( 'opened' );
    }
  },
  mixins: [
    PermissionMixin
  ],
  computed: {
    ...mapState('AuthModule', [
      'user'
    ]),
  },
  mounted() {
    this.$menu = document.querySelector( '#menu' );
  }
}
</script>

<style lang="scss" scoped>
  .app__header {
    min-height: 60px;
  }

  .app__menu {
    position: absolute;
    top: 0;
    left: 100%;
    right: 0;
    min-height: 100vh;
    width: 100vw;
    padding: 30px 20px;
    opacity: 0;
    z-index: 1000;
    transition: all .3s ease-in-out;
    background: #252B33;

    &.opened {
      transform: translate( -100%, 0 );
      opacity: 1;
    }
  }

  .app__hero {
    color: #ffffff;
    font-size: 24px;
    font-weight: bold;
    padding: 15px 0;

    .app__hero-logo {
      margin-left: 53px;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: -27px;
        display: block;
        width: 1px;
        height: 60px;
        background: rgba( #FFFFFF, .31 );
      }
    }

    button {
      &:focus {
        outline: none;
      }
    }

    a {
      color: inherit;
      text-decoration: none;
    }
  }

  .app__menu-list {
    margin-top: 40px;
    padding-left: 0;
    li {
      margin-bottom: 25px;
      list-style: none;
      a {
        color: #fff;
        font-size: 48px;
        font-family: Montserrat;
        font-weight: 500;
      }
    }
  }

  .app__staff{
    margin-right: 20px;
  }

  .app__navigation {
    width: 100%;
  }
  .app__logout {
    width: 96px;

  }
</style>
