const permissions = [
  {
    title: 'stuff1',
    allowedPages: [
      'info', 'search_uuid', 'online', 'mutes'
    ]
  },
  {
    title: 'stuff2',
    allowedPages: [
      'onlineNames'
    ]
  },
  {
    title: 'stuff3',
    allowedPages: [
      'bans', 'reg'
    ]
  },
  {
    title: 'stuff5',
    allowedPages: [
      'uuid', 'last', 'bans_history', 'mutes_history', 'mult'
    ]
  },
  {
    title: 'stuff7',
    allowedPages: [
      'ip', 'bansadm', 'muteadm', 'staff7Buttons', 'mscreen', 'perms', 'tpspawn', 'clear', 'tps'
    ]
  },
  {
    title: 'stuff10',
    allowedPages: [
      'all'
    ]
  },
  {
    title: 'stuff11',
    allowedPages: [
      'all'
    ]
  },
  {
    title: 'stuff12',
    allowedPages: [
      'all'
    ]
  },
  {
    title: 'stuff13',
    allowedPages: [
      'all'
    ]
  }
]

function mixPermissions(stuff) {
  let arr = [];
  const index = permissions.findIndex( item => {
    return item.title === stuff;
  })

  if (index !== -1) {
    if (index === 0) {
      return permissions[index].allowedPages;
    }

    for (let i = 0; i <= index; i++) {
      arr = arr.concat( permissions[i].allowedPages );
    }

    return arr;
  }

  return false;
}

export function getStaffPermissions(stuff) {
  return mixPermissions(stuff) ? mixPermissions(stuff) : false;
}
