<template>
  <input :type="options && options.type ? options.type : 'text'"
         :value="value"
         :id="id"
         :class="options && options.class ? options.class : 'input'"
         autocomplete="off"
         @input="onInput"
         :placeholder="placeholder">
</template>

<script>
export default {
  name: "Input",
  props: {
    value: {
      type: String,
      required: false,
      default: ''
    },
    id: {
      type: String,
      required: true,
    },
    options: {
      type: Object,
      required: false
    },
    placeholder: {
      type: String,
      required: false,
      default: ''
    }
  },
  methods: {
    onInput( event ) {
      this.$emit( 'onInput', event.target.value );
    }
  }
}
</script>

<style lang="scss" scoped>
  input {
    position: relative;
    width: 100%;
    height: 100%;
    border: 1px solid #FD6950;
    font-weight: 500;
    font-size: 18px;
    background: none;
    line-height: 21px;
    color: #FFFFFF;
    padding-left: 28px;
    z-index: 1;

    &.completed {
      background: #FD6950;
    }

    &:focus {
      outline: none;
    }
  }
</style>
